import { HeaderService } from './../../../../../shared/services/header.service';
import { Subscription } from 'rxjs';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { GlobalService } from '../../../../../shared/services/global.service';
import { IbRequestModalData } from '../../../../../shared/models/ib-request.modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { NotificationService } from '../../../../../shared/services/notification.service';


@Component({
  selector: 'app-ib-request',
  templateUrl: './ib-request.component.html',
  styleUrls: ['./ib-request.component.scss']
})
export class IbRequestComponent implements OnInit, OnDestroy {
  public LoggedParams: any;
  profileAccounts: any[] = [];
  IbPartners: any[] = [];
  accountProfile = '';
  subscription1: Subscription;
  ibrequest = new IbRequestModalData();
  requestError;
  requestSuccess;
  requestSuccessMsg;
  order;
  reverse: boolean = true;
  searchText;
   p = 1;
  public Items = 10;
  public TotalCount = 0;
  constructor(
    private global: GlobalService,
    private spinner: NgxSpinnerService,
    private headerService: HeaderService,
    private notificationService: NotificationService
    ) {
      this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
        resData => (this.LoggedParams = resData)
      );
  }

  getLimit(num) {

    this.Items = num;

  }

  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }
getProfileAccounts() {
  const data = {
    ProfileId: this.LoggedParams.sProfile.sMasterId,
    LoginId:  this.LoggedParams.sProfile.sMasterId,
  };
  this.global.getProfileAccounts(data).subscribe((res: any) => {
    this.profileAccounts = res.lstAccount;
  }, err => {
    console.log(err);
  });
}

getIbPartners() {

  const data = {
    MasterId: this.LoggedParams.sProfile.sMasterId,
  };

  this.global.getIbPartner(data).subscribe((res: any) => {

    this.IbPartners = res;

  }, err => {
    console.log(err);
  });

}
copyInputMessage(inputElement) {
  inputElement.select();
  document.execCommand('copy');
  inputElement.setSelectionRange(0, 0);
}
makeIbRequest(account) {
  this.spinner.show();
  this.ibrequest.data.uDealerId = this.LoggedParams.sProfile.sDealerId;
  // tslint:disable-next-line: radix
  this.ibrequest.data.uMasterId = parseInt(this.LoggedParams.sProfile.sMasterId);
  this.ibrequest.data.uAccount = Number(account);

  this.global.addIbreq(this.ibrequest.data).subscribe((res: any) => {
  this.spinner.hide();

  if (res.Result) {
  this.getIbPartners();
  this.requestSuccessMsg = res.MSG_USER;
  // this.requestSuccess = true;
  // tslint:disable-next-line: max-line-length
  this.notificationService.showSuccess(`IB request has been generated successfully for account number ${account}. Please wait for admin approval`, 'success');
  setTimeout(() => {
  // this.requestSuccess = false;
  }, 10000);
} else {
  // this.requestError = true;
this.notificationService.showError('Something went wrong !', 'Error');
setTimeout(() => {
    this.requestError = false;
    }, 10000);
}
  }, err => {
    // this.requestError = true;
this.notificationService.showError('Something went wrong !', 'Error');
setTimeout(() => {
      // this.requestError = false;
      }, 10000);
this.spinner.hide();

  });
}

  ngOnInit(): void {
    // console.log(this.profileData);
    this.getProfileAccounts();
    this.getIbPartners();

  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
    // alert('ngOnDestroy clicked');
  }


}
