import { PartnerAreaCoreComponent } from './partner-area-core/partner-area-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DailyCommissionComponent } from './partner-area-core/daily-commission/daily-commission.component';
import { IbRequestComponent } from './partner-area-core/ib-request/ib-request.component';
import { ViewibComponent } from './partner-area-core/viewib/viewib.component';



const routes: Routes = [
  {
    path: '',
    component: PartnerAreaCoreComponent,
    children: [
      {
        path: 'daily-comission',
        component: DailyCommissionComponent,
        data: {title: 'Partner Area', breadcrumb: 'Daily Comission'}
      },
      {
        path: 'ib-request',
        component: IbRequestComponent,
        data: {title: 'Partner Area', breadcrumb: 'IB Request'}
      },
      {
        path: 'view-ib-and-sub-ib',
        component: ViewibComponent,
        data: {title: 'Partner Area', breadcrumb: 'View IB & Sub IB'}
      }
    ],
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PartnerAreaRoutingModule { }
