import { Component, OnInit } from '@angular/core';
import { GlobalService } from '../../../../../shared/services/global.service';
import { HttpClient } from '@angular/common/http';
import { DatePipe } from '@angular/common';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-daily-commission',
  templateUrl: './daily-commission.component.html',
  styleUrls: ['./daily-commission.component.scss']
})
export class DailyCommissionComponent implements OnInit {
  datechoose;
  datechoose1;
  partners: any[]=[];
  accountProfile = '';
  comissions: any[] = [];
  total_com;
  total_lot;
  total_trades;
  order;
  reverse:boolean = true;
  searchText;
   p = 1;
  public Items = 10;
  public TotalCount = 0;
  constructor(private global: GlobalService,private http:HttpClient,public datepipe: DatePipe,private spinner: NgxSpinnerService) { }


  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }
  getLimit(num){

    this.Items = num;

  }
  getIbPartners(){

    const data = {
      MasterId: localStorage.getItem('LoggedDealermasterid')
    }

    this.global.getIbPartner(data).subscribe((res: any) =>{

       this.partners = res;
       console.log(this.partners);

    });

  }

  dailyComission(acc){
this.spinner.show();
    const data = {
      Account: acc,
      dtFrom:this.datepipe.transform(this.datechoose, 'yyyy-MM-dd'),
      dtTo:this.datepipe.transform(this.datechoose1, 'yyyy-MM-dd')
    }

    this.global.getIBnew(data).subscribe((res: any) =>{
      this.spinner.hide();

        this.comissions = res.lstCommission;
        this.total_com = res.Commission;
        this.total_lot = res.Lot;
        this.total_trades = res.Trades;

    }, err =>{
this.spinner.hide();
    })

  }


  ngOnInit(): void {

    this.getIbPartners();

  }

}
